	module.exports = {"data":{"markdownRemark":{"html":"<p>最近このブログの方向性が迷走しているうえに、書く記事を慎重に選びすぎて、更新もほとんどままならない状態になっています。</p>\n<p>そこで、改めて方向性を書き出して、決めていこうかなと思います。\n基本的には、下記の6カテゴリをメインとしていきます。</p>\n<ol>\n<li>当然同人サークルのオフィシャルサイトなので、自作同人作品(【東方、ボカロ、オリジナル】なゲームが主で、たまにイラストやラノベなんかも)の話や、その制作話がメイン</li>\n<li>ゲーム制作つながりで、プログラミングや作業環境を主とした技術メモとか</li>\n<li>さらにゲームつながりで、最近のゲーム動向とか(アンテナ狭いので頻度低めかも)</li>\n<li>作業環境つながりで人柱向けのガジェット情報とか(これもアンテナ狭ry)</li>\n<li>ここ半年くらいで活動始めた、【作る側から教える側へのシフト】に関する活動とか</li>\n<li>これまた去年くらいから始めた、【『働く』を勉強しよう】っぽい話とか</li>\n<li>あとはテキトーな日記とか、場合によっては自分の Twitter のまとめとか(重要)</li>\n</ol>\n<p>特に一番最後がないと、絶対更新止まるということをここ数年で思い知った(笑)<br>\nテキトーな日記を書くと、方向性がぼやけるためマイナスに作用しやすいですが、更新が止まる故のマイナスに比べると微々たるものなので、これは外さないほうがよさげですね。</p>\n<p>それと <a href=\"/legacy\">danmaqlassic</a> からの記事の移行も早くすませないとね。\nまだ<a href=\"/category/unfiled\">全記事数のうち 4割 は機械的なマイグレーションをしただけ</a>なので、まともに読めるような状態じゃなかったりしてます……。</p>","frontmatter":{"cover":null,"date":"2016-07-07T21:34:17+09:00","strDate":"2016/7/7","title":"方向性を改めて考えてみる"}}},"pathContext":{"path":"/ja/blog/2016/07/07-vector-of-danmaq/","langKey":"ja"}}

